<template>
  <div>
    <div>
      <el-card shodow='hover' class='card'>
        <el-divider class='divider' content-position="right" @click='backMarketList'>
          <div class='back'>
            <el-image class='backIcon' :src="require('@/../public/assets/return.png')"></el-image>
            <span class='backWord'>{{$t('Market.NewReturn')}}</span>
          </div>
        </el-divider>

        <div class='new'>
          <div class='itemInfo'>
            <el-input class='inputBox name' :placeholder="this.$t('Market.NewName')" minlength="2" maxlength="16"
              v-model='itemName'></el-input>
            <el-input class='inputBox version' :placeholder="this.$t('Market.NewVersion')" minlength="1" maxlength="10"
              v-model='itemVersion'></el-input>
            <el-input class='inputBox publish' :placeholder="this.$t('Market.NewPublish')" minlength="5" maxlength="16"
              v-model='itemPublish'></el-input>
          </div>

          <div class='schoolClass'>
            <el-input class='inputBox school' :placeholder="this.$t('Market.NewSchool')" minlength="2" maxlength="16"
              v-model='showSchool' disabled></el-input>
            <el-input class='inputBox class' :placeholder="this.$t('Market.NewClass')" minlength="2" maxlength="16"
              v-model='classname'></el-input>
          </div>

          <div class='uploadPic'>
            <el-upload class="uploader" action="server.default.uploadImg" :show-file-list="false"
              :http-request="uploadPicture" :before-upload="beforePictureUpload">
              <img v-if='picture' :src='picture' class="uploadImage" />
              <i v-else class="el-icon-plus uploadIcon"></i>
            </el-upload>
          </div>

          <div class='price'>
            {{this.$t('Market.NewPrice')}}
            <el-input-number class='priceInput' v-model="price" :min="0.01" :max="99.99" controls-position='right'
              :precision="2" :step="0.10" @change='priceSetTip'></el-input-number>
          </div>

          <div class='itemStatus'>
            <div class='itemStatusItem broken'>
              <span>{{this.$t('Market.NewBroken')}}</span>
              <el-slider class='silder' v-model="broken" :format-tooltip="formatTooltip" :step="5"
                show-stops></el-slider>
            </div>
            <div class='itemStatusItem written'>
              <span>{{this.$t('Market.NewWritten')}}</span>
              <el-slider class='silder' v-model="written" :format-tooltip="formatTooltip" :step="5"
                show-stops></el-slider>
            </div>
          </div>

          <div class='buttons'>
            <el-button class='button' type='primary' @click='submit'>{{$t('Hollow.NewConfirm')}}</el-button>
          </div>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import Cookies from 'js-cookie'
import * as server from '../../../server.js'
import { customAlphabet } from 'nanoid';
const nanoid = customAlphabet('1234567890', 9);

export default {
  name: 'market-new',
  data () {
    return {
      userID: 0,
      itemName: '',
      itemVersion: '',
      itemPublish: '',
      classname: '',
      showSchool: '',
      school: 0,
      price: 5,
      broken: 0,
      written: 0,
      picture: '',
      pictureRaw: '',
      priceSetTime: 0
    }
  },
  methods: {
    backMarketList () {
      this.$bus.$emit('showMarketList')
    },
    submit () {
      if (this.itemName == '' || this.itemVersion == '' || this.itemPublish == '' || this.classname == '' || this.pictureRaw == '') {
        this.alert(this.$t('Market.NewConfirmInfoEmpty'), this.$t('System.Error'));
        return;
      }
      else if (this.itemName.length > 10 || this.itemVersion.length > 5 || this.itemPublish.length > 10 || this.classname.length > 8) {
        this.alert(this.$t('Market.NewConfirmInfoLong'), this.$t('System.Error'));
        return;
      }
      else {
        this.$confirm(this.$t('Market.NewConfirmAsk'), this.$t('System.Warning'), {
          confirmButtonText: this.$t('System.Yes'),
          cancelButtonText: this.$t('System.No'),
          type: 'warning'
        }).then(() => {
          const loadingInstance = this.loading();

          axios({
            method: 'post',
            url: server.default.postMarket,
            data: new URLSearchParams({
              'itemid': nanoid(),
              'userid': this.userID,
              'itemname': this.itemName,
              'itemversion': this.itemVersion,
              'itempublish': this.itemPublish,
              'classname': this.classname,
              'school': this.school,
              'price': this.price,
              'broken': this.broken,
              'written': this.written,
              'picture': this.pictureRaw
            })
          }).then((response) => {
            loadingInstance.close();
            if (response.status != 200) {
              this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
            }
            else {
              if (response.data == 'MARKET_ITEMID_TAKEN') {
                this.submit();
                return;
              }
              else if (response.data.status == 'MARKET_POST_COMPLETE') {
                this.$bus.$emit('refreshMarketList');
                this.alert(this.$t('Market.NewCompleteJump'), this.$t('Market.NewComplete'), () => {
                  this.$bus.$emit('showMarketDetail', response.data.itemid);
                })
              }
            }
          })
        }).catch(() => {
          return;
        });
      }
    },
    getSchool () {
      const loadingInstance = this.loading();

      axios({
        method: 'post',
        url: server.default.user,
        data: new URLSearchParams({
          'type': 'me',
          'cookie': Cookies.get('loginAs')
        })
      }).then((response) => {
        loadingInstance.close();
        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data == 'USER_NOMATCH') {
            this.alert(this.$t('System.IllegalRequest'), this.$t('System.Error'));
          }
          else if (response.data.status == 'USER_FOUND') {
            this.userID = response.data.userid;
            this.school = response.data.school;
            this.showSchool = this.formatSchool(response.data.school)
          }
        }
      });
    },
    priceSetTip () {
      this.priceSetTime++;
      if (this.priceSetTime > 5) {
        this.$message(this.$t('Market.NewPriceTip'))
        this.priceSetTime = 0;
      }
    },
    formatTooltip (val) {
      return val / 100;
    },
    uploadPicture (file) {
      const loadingInstance = this.loading();

      let formData = new FormData();
      formData.append("file", file.file);
      formData.append("type", "market");
      formData.append("cookie", Cookies.get('loginAs'))

      axios({
        method: "post",
        url: server.default.uploadImg,
        data: formData
      }).then((response) => {
        loadingInstance.close();

        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data.status == 'IMAGE_UPLOAD_SUCCESS') {
            this.picture = server.default.server + response.data.id;
            this.pictureRaw = response.data.id;
          }
        }
      });
    },
    beforePictureUpload (file) {
      if (file.type != 'image/jpeg' && file.type != 'image/png') {
        this.alert(this.$t('Market.PictureWrongType'), this.$t('System.Warning'));
        return false;
      }
      else if (file.size / 1024 / 1024 > 2) {
        this.alert(this.$t('Market.PictureTooLarge'), this.$t('System.Warning'));
        return false;
      }
      else {
        return true;
      }
    },
  },
  mounted () {
    this.getSchool();
  }
}
</script>

<style scoped>
.card {
  margin-left: 5%;
  width: 90%;
  border-radius: 20px;
}
.divider {
  width: 80%;
  margin-left: 10%;
}
.back {
  cursor: pointer;
}
.backIcon {
  color: rgb(252, 62, 5);
  width: 20px;
}
.backWord {
  font-size: 18px;
  margin-left: 10px;
}
.new {
  width: 80%;
  margin-left: 10%;
}
.inputBox {
  margin-top: 30px;
  font-size: 20px;
}
.inputBox ::v-deep .el-input__inner {
  border-radius: 10px;
}
.version,
.school {
  width: 40%;
  float: left;
}
.publish,
.class {
  width: 55%;
  float: right;
}
.uploadPic {
  margin-top: 170px;
  margin-bottom: 30px;
  text-align: left;
}
.uploadPic .uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.uploadPic .uploader .el-upload:hover {
  border-color: #409eff;
}
.uploadPic .uploader .uploadIcon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
  border: 1px solid gray;
  border-radius: 15px;
}
.uploadPic .uploader .uploadImage {
  width: 178px;
  height: 178px;
  display: block;
  border: 1px solid gray;
  border-radius: 15px;
}
.price {
  text-align: left;
  font-size: 20px;
}
.priceInput {
  width: 50%;
  margin-left: 10%;
  font-size: 20px;
}
.priceInput ::v-deep .el-input__inner {
  font-size: 20px;
}
.itemStatus {
  text-align: left;
  margin-top: 15px;
  margin-bottom: 30px;
}
.itemStatusItem {
  margin-top: 15px;
  margin-bottom: 15px;
}
.silder {
  width: 70%;
  display: inline-block;
  margin-left: 10%;
  position: relative;
  top: 10px;
}
.button {
  font-size: 20px;
  border-radius: 15px;
}
</style>